import React from "react";
import ReactDOM from "react-dom";
import Marked from "marked";
import ReactHtmlParser from "react-html-parser";
import "./index.css"

const Preview = (props) => {
  return (
    <div id="preview"className="Preview">
      {props.markedText}
    </div>
  );
}

class Editor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      preMarkedText : "# for H1!\n## for H2!\n[for links](https://www.freecodecamp.org/learn)\n\n`<p>this is inline code</p>`\n```\n<h1>Hello world! this is a code block</h1>\n```\n* listItem\n\n>BlockQuote\n\n**this is an image of markdown logo:** ![Markdown logo](https://upload.wikimedia.org/wikipedia/commons/thumb/4/48/Markdown-mark.svg/1280px-Markdown-mark.svg.png)"
    }
  }

  handleChange = (event) => {
    this.setState({
      preMarkedText: event.target.value
    })
  }

  render() {
    const markedText = ReactHtmlParser(Marked(this.state.preMarkedText));
    return (
      <div>
        <h2 style={{textAlign:"center"}}>Please type your Markdown below:</h2>
        <textarea
          id="editor" 
          value={this.state.preMarkedText}
          onChange={this.handleChange}
          placeholder="Enter your Markdown here..."
          cols="50"
          rows = "10"
          className = "Editor"
        />
        <hr />
        <h2>The preview: </h2>
        <Preview markedText={markedText}/>
      </div>
    );
  }
}


ReactDOM.render(<Editor />, document.getElementById("root"));